body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.toolbar-select {
  outline: none;
  border: none;
  background: transparent;
  font-size: 16px;
}

.App {
  height: 100vh;
  overflow: hidden;
}

.pdf-viewer {
  height: 100%;
  width: 100%;
  flex: 1;
  overflow: auto;
  background: #EAEAEA;
}

.page-layer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.pdf-navigator {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  height: 36px;
  z-index: 9999;
  border-radius: 6px;
  background: #FDFDFD;
  width: 160px;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.20);
  display:flex;
  justify-content: space-around;
  align-items: center;
  padding: 4px;
}


.pdf-text-selection-overlay {
  z-index: 9999;
  border-radius: 6px;
  background: #FDFDFD;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.20);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 4px;
  pointer-events: none;
  position: absolute;
  display: "block";
  z-index: 99;
  transform: translate(-100%,5px);
}

.pdf-viewer-layout {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: relative;
  height: 100%;
  width: 100%;
}

::selection {
  -webkit-user-drag: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  mix-blend-mode: difference;
  background: #C9E3FF;
}

.pdf-viewer-sidebar-container {
  flex-basis: 200px;
  height: 100%;
  flex-shrink: 0;
  display:flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  border-right: 1px solid #D6D6D6;
  background-color: #FBFBFB;
}
.pdf-viewer-container {
  flex: 1;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}


.thumbnail {
  width: 150px;
  margin: 10px 10px 0px 10px;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.10);
}

.sidebar {
  flex: 0 0 200px;
  height: 100%;
  border-right: 1px solid #D6D6D6;
  background: #FBFBFB;
  overflow: auto;
}
 
.toolbar-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #FBFBFB;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #D6D6D6;
}

.toolbar-button {
  outline: none;
  border: none;
  background-color: transparent;
  transition: all 0.2s ease-in-out;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  cursor: pointer;
  margin-left: 2px;
  margin-right: 2px;
  pointer-events: auto;
}
.toolbar-button:disabled {
  opacity: 0.2;
  cursor: auto;
}
.toolbar-button:disabled:hover {
  background-color: transparent;
}

.pdf-navigator-counter {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-left: 8px;
  padding-right: 8px;
  flex: 1;
}

.toolbar-button:hover {
  background-color: #d5d5d5;
}

.toolbar-container .navigation,
.toolbar-container .zoom {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  padding: 4px;
}

.toolbar-container .navigation > div {
  width: 48px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-rect {
  display: block;
  color: transparent;
  font-size: 16px!important;
  font-weight: 400;
  transform-origin: 0 0 0;
  font-family: Arial, sans-serif;
  line-height: 1;
  position: absolute;
  text-align: left;
  white-space: pre;
  margin: 0;
  opacity: 0.7;
}

.render-layer {
  width: 100%;
  height: 100%;
  display: block;
}

.page-container {
  position: relative;
  margin: 0 auto;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.10);
}

.loading-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.lds-ring {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #777777;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #777777 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}